<template>
  <div class="home_index">
    <div class="home">
      <img alt="Vue logo" src="../assets/logo.png"/>
      <input ref="photoref" type="file" accept="image/*" @change="Photograph()" capture="camera"/>
      <p>{{ fileName }}</p>
    </div>
    <div class="preview">
      <img :src="base64ImgData" alt=""/>
    </div>
  </div>
</template>

<script>

export default {
  name: 'home',
  data () {
    return {
      fileName: 'Vue',
      base64ImgData: null
    }
  },
  methods: {
    /**
     * 
     */
    async Photograph () {
      // 
      this.fileName = this.$refs.photoref.files[0].name
      // base64  base64ImgData 
      this.base64ImgData = await this.FileReader(this.$refs.photoref.files[0])
    },
    /**
     * base64
     */
    FileReader (FileInfo) {
      // FileReader https://developer.mozilla.org/zh-CN/docs/Web/API/FileReader
      let reader = new FileReader()
      // readAsDataURL https://developer.mozilla.org/zh-CN/docs/Web/API/FileReader/readAsDataURL
      reader.readAsDataURL(FileInfo)
      // 
      /* eslint-disable */
      return new Promise(resolve => reader.onloadend = () => resolve(reader.result))
    }
  },
  components: { }
}
</script>

<style scope>
.home {
  width: 200px;
  margin: 0 auto;
  position: relative;
}
.home input[type="file"] {
  position: absolute;
  left: 0;
  top: 0;
  width: 200px;
  height: 200px;
  z-index: 9;
  opacity: 0;
}
.home p {
  font-size: 13px;
  color: #8d8d8d;
}
.preview img {
  width: 100%;
}
</style>
